export default{
	data(){
		return{
			shopobj:{},
			shopAddress:{},
			refunObj:{},
			refundState:["申请中","同意","拒绝","处理中","完成"],
			orderLineId:"",
			step:1,//当前步骤
			examine:"1",//审核结果
			Reject:"",//驳回原因
		}
	},
	methods:{
		init(){
			let that=this;
			that.$ajax({
				url:"/order/refundDetail",
				data:{
					orderLineId:that.orderLineId
				},success(d){
					if(!d){
						uni.showToast({
							title:"退款信息获取失败",
							icon:"none"
						})
						return
					}
					if(d.refundImgs){
						d.refundImgs=d.refundImgs.split(",")
					}
					that.refunObj=d;
					that.step=d.refundState;
					if(that.step==0){
						that.searchAddress()
					}
				}
			})
		},
		radioChange(e){
			this.examine=e.detail.value;
			this.Reject="";
		},
		//修改地址
		editAddress(){
			uni.navigateTo({
				url:"/pages/system/addAddress"
			})
		},
		//查询商家地址
		searchAddress(){
			let that=this;
			that.$ajax({
				url:"/merchant/returnAddress",
				data:{
					merchantId:that.shopobj.merchantId,
				},success(d){
					that.shopAddress=d
				}
			})
		},
		//退款审核
		refundAudit(){
			let that=this;
			that.$ajax({
				url:"/order/refundAudit",
				data:{
					desc:that.Reject||"",
					infoId:that.shopAddress.infoId,
					managerId:that.shopobj.managerId,
					merchantId:that.shopobj.merchantId,
					orderId:that.refunObj.orderId,
					orderLineId:that.refunObj.orderLineId,
					refundId:that.refunObj.refundId,
					state:that.examine,
				},
				success(d){
					that.init();
				}
			})
		},
		//退款
		refundOk(){
			let that=this;
			uni.showModal({
				title: '温馨提示',
				content: '是否确定退款',
				success: function (res) {
					if (res.confirm) {
						that.$ajax({
							url:"/order/refund",
							data:{
								orderId: that.refunObj.orderId,
								orderLineId: that.refunObj.orderLineId
							},
							success(d){
								that.init()
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 预览图片
		previewImage(index){
			uni.previewImage({
				current:index,
				urls: this.refunObj.refundImgs,
				loop:true,
				longPressActions: {
					itemList: [],
					success: function (res) {
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			});
		}
	},
	onNavigationBarButtonTap(e){
		let that=this;
		if(that.refunObj.phone){
			uni.makePhoneCall({
				phoneNumber: that.refunObj.phone,
				fail(err){
					uni.showToast({
						title:err,
						icon:"none"
					})
				}
			});
		}else{
			uni.showToast({
				title:"用户没有填写手机号码"
			})
		}
	
	},
	onLoad(e){
		let that=this;
		that.orderLineId=e.id||"";
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopobj=JSON.parse(res.data);
			}
		})
	},
	onShow(){
		this.init();
	}
}
